div.app {
    --zoom-box: 1;
    --zoom-text: 1;
}

div.station {
    position: absolute;
    border: .01in outset grey;
    font-size: calc(.3in * var(--zoom-text));
    width: calc(2in * var(--zoom-box));
    white-space: pre;
    overflow: hidden;
    background: white;
}

div.station span.rn {
    color: white;
    text-shadow: 0 0 .1vw black;
    font-weight: bold;
    width: 1em;
}

div.station span.half {
    width: calc(50% - 0.5em);
    overflow: hidden;
    display: inline-block;
    background: linear-gradient(to left, #333, #333 50%, #eee 75%, #333 75%);
}

div.ball {
    width: calc(.2in * var(--zoom-text));
    height: calc(.2in * var(--zoom-text));
    border-radius: 50%;
    text-align: center;
    color: white;
    font-size: calc(.2in * var(--zoom-text));
    text-shadow: 0 0 .1vw black;
    position: absolute;
    z-index: -1;
}
